<script>
import cloneDeep from 'lodash/cloneDeep'
export default {
  name: 'cc-cascader',
  components: {},
  props: {
    // 选中的值
    value: {
      type: [String, Number],
      default: ''
    },
    // 选项数组
    options: {
      type: Array,
      required: true
    },
    // 标题
    title: {
      type: String,
      default: '请选择'
    },
    // 激活时颜色
    activeColor: {
      type: String,
      default: '#ee0a24'
    },
    // 显示关闭按钮
    closeable: {
      type: Boolean,
      default: true
    },
    // 自定义 options 结构中的字段
    fieldNames: {
      type: Object,
      default: () => {
        return {
          text: 'text',
          value: 'value',
          children: 'children'
        }
      }
    }
  },
  data() {
    return {
      // 标题栏
      tabs: [
        {
          title: '请选择'
        }
      ],
      // 当前选中项
      current: 0,
      // 当前点击项下标
      currentIndex: -1,
      currentValue: '',
      move: 0,
      list: cloneDeep(this.options),
      // 给数据每一项标识 表示是第几层数据
      index: 0,
      // 树形结构数据层级
      level: 0
    }
  },
  methods: {
    addTitle() {
      this.tabs.unshift({
        title: this.currentValue
      })
    },
    clickTitle(item, index) {
      if (this.current === index) return
      if (index < this.current) {
        this.current--
        this.lineMove()
      }
      if (index > this.current) {
        this.current++
        this.lineMove()
      }
    },
    // 点击每一项
    clickItem(item, index) {
      this.current++
      this.currentValue = item.text
      this.currentIndex = index
      this.lineMove()
      if (item.added) {
        this.addTitle()
      } else {
        this.$set(this.tabs, this.current - 1, {title: item.text})
        console.log(this.tabs)
      }
      this.list.map(i => {
        if (i.level === item.level) {
          i.added = false
        }
      })
    },
    init(data) {
      data.map((item, index) => {
        this.$set(item, 'added', true)
        if (item.children && item.children.length) this.init(item.children)
      })
      this.index++
    },
    // 滑块移动
    lineMove() {
      this.$nextTick(() => {
        uni
          .createSelectorQuery()
          .in(this)
          .select(`#cc-cascader-${this.current}`)
          .boundingClientRect()
          .exec(res => {
            this.move = res[0].left - 16
          })
      })
    }
  },
  mounted() {
    this.lineMove()
    this.level = this.$utils.getMaxlevel(this.list)
    this.init(this.list)
  },
  onLoad() {},
  onShow() {},
  filters: {},
  computed: {},
  watch: {}
}
</script>

